  import uniIcon from '@/components/uni-icon/uni-icon.vue';
  import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue';
  export default {
    data() {
      return {
        productId:null,
        detailInfo:" ",
        specClass: 'none',
        specSelected:[]
      };
    },
    onLoad(e) {
      this.productId = e.id;
      this.fetchData();
    },
    methods:{
      fetchData(){
        var url = "goods/detail";
        var data = {
          id:this.productId
        }  
        this.$Request.get(url,data).then( res => {
          if(res && res.code == 200 && res.data){
            this.detailInfo = res.data.goodsInfo;
            console.log(res);
          }
        })
      },
      //规格弹窗开关
      toggleSpec() {
      	if(this.specClass === 'show'){
      		this.specClass = 'hide';
      		setTimeout(() => {
      			this.specClass = 'none';
      		}, 250);
      	}else if(this.specClass === 'none'){
      		this.specClass = 'show';
      	}
      },
      //选择规格
      selectSpec(childIndex,childId,parentIndex){
         
       let list = this.detailInfo.spec_data[parentIndex].child;

				// 按钮点击切换
        list.forEach( item => {
           if(item.id === childId && !item.selected){
						 this.$set(item,'selected',true);
					 }else{
						 this.$set(item,'selected',false);
					 }
        })
				
				//存储已选择
				// 每次点击按钮 先清空specSelected  再遍历child中selected为true的对象  添加到specSelected数组中
				this.specSelected = []; 
				this.detailInfo.spec_data.forEach(item=>{ 
					item.child.forEach( childItem => {
						 if(childItem.selected === true){
							 	this.specSelected.push(childItem); 
						 }
					})
				})
      },
			//选择购买数量
			buyCount(e){
				console.log(e);
			},
			//加入购物车
			insertCart(){
				uni.showToast({
					title:"已加入购物车!",
					icon:"success"
				})
			},
      stopPrevent(){}
    },
    components:{
      uniIcon,
      uniNumberBox
    }
  }
